import React, { Component } from "react";
import axios from 'axios';
class UploadPhoto extends Component {

    state = {picUrl: this.props.picUrl}

    track = (event) => {
        this.changeStatus(true, "gray", false,"Confirm Upload.");
    }
    componentDidMount()
    {
        this.checkBox = document.getElementById("checkUp");
        this.photoFile = document.getElementById("file");
        this.confirm = document.getElementById("confirmUp");
        this.confirmDelete = document.getElementById("confirmDelete");
        this.confirmDeleteCheck = document.getElementById("deleteCheckBox");
        this.profilepic = document.getElementById("profilepic");
        this.pic = document.getElementById("pic");
        
        //this.state.picUrl = this.updateProfilePic();
    }
   constructor(props) {
        super(props);
        console.log("current url uploadphoto:"+this.props.picUrl);
        this.baseUrl = "https://qacvmanager.azurewebsites.net/api";
        this.deleteUrl = this.baseUrl+"/user/"+ this.props.userId + "/picture/delete";
        this.uploadPhoto = (e) => {
            e.preventDefault();
            if (this.changeStatus(false)) {
                this.changeStatus(true, "limegreen", false,"Uploading file...");
                var cvName = this.photoFile.files[0].name;
                var setPostLink = this.baseUrl+"/user/" +this.props.userId + "/picture/";
                var formData = new FormData();
                 formData.append("file", this.photoFile.files[0]);

               var req = axios.post(setPostLink, formData,
                {
                  headers: {
                        'Content-Type': 'multipart/form-data'
                      }
                });
                req.then(response => {
                    if (response.status === 200) 
                    {
                        this.changeStatus(true, "limegreen", false,cvName+" uploaded Successfully.");
                        this.photoFile.value = null;
                        this.setState({picUrl: `${this.baseUrl}/user/${this.props.userId}/picture/download?t=${new Date()}`});
                        this.props.updateProfilePic();
                    }
                })
            }
            else
            {
                if(this.photoFile.files[0] === undefined)
                {
                    this.changeStatus(true,"red", false,"Please Choose a File.");
                    this.photoFile.focus();
                } 
                else
                {
                    this.changeStatus(true,"red", false,"Confirm Upload.");
                }
            }
        }
    }
	// updateProfilePic()
	// {
    //     axios.get(`${this.baseUrl}/user/${this.props.userId}/picture/download`)
    //     .then((response) =>
    //     {
    //         console.log("picture: "+response);
    //         console.log("picture data: "+response.data);
    //         console.log("response status: "+response.status);
    //         if(response.status === 200)
    //         {
    //             this.setState({picUrl: `${this.baseUrl}/user/${this.props.userId}/picture/download?t=${new Date()}`});
    //             console.log("loaded: "+ this.state.picUrl);
    //         }
           
    //     }).catch(() =>
    //     {
    //         this.setState({picUrl: "https://image.flaticon.com/icons/svg/149/149071.svg"});
    //     });
	// }
  changeStatus (change,colour, checked, text){
        if(change)
        {
            this.confirm.style="color:"+colour;
            this.confirm.textContent = text;
            this.checkBox.checked = checked;
        }
        else return (this.checkBox.checked && this.photoFile.files[0]!==undefined);
    }
    changeStatusDelete (change,colour,checked, text){
        
        if(change)
        {
            this.confirmDelete.style="color:"+colour;
            this.confirmDelete.textContent = text;
        }
        else return (this.confirmDeleteCheck.checked);
      }
    deletePhoto = (e) => {
        e.preventDefault();
        if(this.changeStatusDelete(false))
        {
            this.changeStatusDelete(true,"limegreen",false,"Deleting photo...");
            axios.put(this.deleteUrl).then(() => {
            this.changeStatusDelete(true,"limegreen",false,"Deleted.");
            this.setState({picUrl: "https://image.flaticon.com/icons/svg/149/149071.svg"});
            this.props.updateProfilePic();
            }).catch((error) => {
            this.changeStatusDelete(true,"red",false, "Unable to delete photo.");
            })
        }
        else
        {
            this.changeStatusDelete(true,"red", false,"Confirm Delete.");
        }
      }
  render() {
        return ( 
        <div className="wrap-input100">
            <div className="wrap p-l-85 p-r-85 p-t-55 p-b-55">
             <div className = "row m-t-30">
                    <div className = "col">
                        <div className = "m-b-12" data-validate="Check is required">
                            <button className="login100-form-btn" onClick = {this.deletePhoto}>Delete Photo</button>
                        </div>
                        <input  className="input-checkbox100" id="deleteCheckBox" type="checkbox" name="correct-info" />
                        <label className="label-checkbox100" htmlFor="deleteCheckBox">
                                <span className="lnr lnr-envelope" id = "confirmDelete">Confirm Delete.</span>
                        </label>
                    </div>
                </div>
                <div className="row m-b-12-">
                    <img style = {{width: "250px", height: "250px"}}className = "col" id ="profilepic" alt = "lol" src = {this.state.picUrl}  className="img-responsive"/>
                </div>
                <div className = "row m-t-30">
                    <div className = "col-sm-8">
                        <div className="validate-input m-b-12 login100-form-btn" data-validate="Picture is required" >
                            <input className="input100" id="file"type="file" name="file"onChange = {this.track}/>
                            <span className="focus-input100"></span>
                        </div>
                        <input  className="input-checkbox100" id="checkUp" type="checkbox" name="correct-info" />
                        <label className="label-checkbox100" htmlFor="checkUp">
                                <span className="lnr lnr-envelope" id = "confirmUp">Confirm Upload.</span>
                        </label>
                    </div>
                    <div className = "col">
                         <div data-validate="Ceck is required">
                            <button    className="login100-form-btn" onClick = {this.uploadPhoto}>Upload</button>
                        </div>
                    </div>
                </div>
               
            </div>
        </div>
        );
  }
}
export default UploadPhoto;
